@import '0-abstracts/variables';
@import '0-abstracts/mixins';

:root {

    /* Dark Color */
    --tt-dark-color: #F2F2F2;
    --tt-dark-color-rgb: 242, 242, 242;
    --tt-dark-color-h: 0;
    --tt-dark-color-s: 0%;
    --tt-dark-color-l: 50%;

      /* White Color */
    --tt-white-color: #212121;
    --tt-white-color-rgb: 33, 33, 33;
    --tt-white-color-h: 0;
    --tt-white-color-s: 0%;
    --tt-white-color-l: 13%;

    /* Gray Color */
    --tt-gray-color: #BCBCBC;
    --tt-gray-color-rgb: 188, 188, 188;

    /* Gray Light Color */
    --tt-gray-light-color: #919191;
    --tt-gray-light-color-rgb: 145, 145, 145;

    /* Light Color */
    --tt-light-color: #121212;
    --tt-light-color-rgb: 18, 18, 18;

    /* Border Color */
    --tt-border-color: #2e2e2e;

    /* Border Light Color */
    --tt-border-light-color: #202020;

    /* Dark Theme Alt Colors */
    --tt-darktheme-white: #fff;
    --tt-darktheme-alt-white: #212121;
    --tt-darktheme-alt-light: #121212;
    --tt-darktheme-alt-dark: #F2F2F2;
    --tt-darktheme-dark-darken: #d5d5d5;
    --tt-darktheme-alt-gray: #BCBCBC;
    --tt-darktheme-alt-gray-light: #919191;
    --tt-darktheme-alt-border-color: #2e2e2e;
    --tt-darktheme-alt-border-light-color: #272727;

    /* Dark Theme Image Opacity */
    --tt-darktheme-img-opacity: 0.8;
}

a {
    &:hover {
        color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) + 10%))
    }
}

.darktheme-img {
    opacity: var(--tt-darktheme-img-opacity);
    transition: $transition-base;

    &:hover {
        opacity: 1;
    }
}

.media-card:hover .media-card__image {
    opacity: 1;
}

.media-card__image {
    opacity: var(--tt-darktheme-img-opacity);
}

.btn-dark {
    &:hover {
        background-color: hsl(var(--tt-dark-color-h), var(--tt-dark-color-s), calc(var(--tt-dark-color-l) + 25%));
        border-color: hsl(var(--tt-dark-color-h), var(--tt-dark-color-s), calc(var(--tt-dark-color-l) + 25%));
    }
}

.form-control, .form-select, .form-check-input, .select2-dropdown,
.select2-dropdown .select2-search__field,
.select2 .select2-selection {
    border-color: #4b4b4b !important;
}

.form-select, .select2 .select2-selection {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23f2f2f2' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
}

.accordion-button {
    &::after {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23f2f2f2'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
    }

    &:not(.collapsed) {
    
        &::after {
            background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23f2f2f2'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e") !important;
        }
    }
}

.my-program .accordion .accordion-item-placeholder::before {
    background-color: #494949;
}

tr.bg-primary.text-white th {
    color: #fff;
}

hr {
    background-color: var(--tt-border-light-color);
    opacity: 1;
}

.messages {

    &__item-status {
        border-color: #3a3a3a;
    }
}

.owl-theme .owl-dots .owl-dot span {
    background-color: #515151;
}

.btn-close {
    background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23f2f2f2'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
    opacity: 0.75;

    &:hover {
        opacity: 1;
    }
}

.pagination .page-item .page-link {
    color: #fff;
}

.alert {
    color: var(--tt-dark-color);
}

.alert-info {
    background-color: rgba(var(--tt-blue-color-rgb), 0.15);
    border-color: rgba(var(--tt-blue-color-rgb), 0.25);
}

.alert-success {
    background-color: rgba(var(--tt-green-color-rgb), 0.15);
    border-color: rgba(var(--tt-green-color-rgb), 0.25);
}

.alert-danger {
    background-color: rgba(var(--tt-red-color-rgb), 0.15);
    border-color: rgba(var(--tt-red-color-rgb), 0.25);
}

.alert-warning {
    background-color: rgba(var(--tt-yellow-color-rgb), 0.15);
    border-color: rgba(var(--tt-yellow-color-rgb), 0.25);
}


.select2-container--default[dir=rtl] .select2-selection--multiple .select2-selection__choice {
    border-color: #4b4b4b;
}